.container {
  overflow-y: auto;
  font-size: 14px;
  flex: 1;
}

.nodeWrapper {
  display: flex;
  flex-direction: column;
  user-select: none;
}

.nodeContent {
  display: flex;
  align-items: center;
  height: 32px;
  cursor: pointer;
  padding: 0 8px;
  border-radius: 4px;
  transition: background-color 0.2s ease;
}

.nodeContent:hover {
  background-color: var(--color-surface-secondary);
}

.toggle {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  margin-right: 4px;
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
  color: var(--color-text-secondary);
  transition: color 0.2s ease;
}

.toggle:hover {
  color: var(--color-text);
}

.toggleIcon {
  transition: transform 0.2s ease;
  transform: rotate(-90deg);
}

.toggleIcon.expanded {
  transform: rotate(0deg);
}

.checkboxWrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  margin-right: 4px;
}

.checkbox {
  width: 16px;
  height: 16px;
  cursor: pointer;
  accent-color: var(--color-primary);
}

.label {
  display: flex;
  align-items: center;
  gap: 8px;
  flex: 1;
  color: var(--color-text-secondary);
  cursor: pointer;
  font-size: 13px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.fileLabel {
  color: var(--color-text);
}

.fileLabel.selected {
  color: var(--color-primary);
  font-weight: 500;
}

.folderLabel {
  font-weight: 500;
  color: var(--color-text);
}

.name {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.children {
  display: flex;
  flex-direction: column;
}
