.container {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: var(--color-background);
  border-left: 1px solid var(--color-border);
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px;
  border-bottom: 1px solid var(--color-border);
  gap: 16px;
  flex-wrap: wrap;
  flex-shrink: 0;
}

.headerLeft {
  display: flex;
  align-items: center;
  gap: 12px;
  min-width: 0;
}

.title {
  font-size: 14px;
  font-weight: 600;
  color: var(--color-text);
  margin: 0;
}

.headerRight {
  display: flex;
  gap: 8px;
}

.copyButton,
.saveButton {
  padding: 8px 16px;
  background-color: var(--color-primary);
  color: white;
  border: none;
  border-radius: var(--radius-md);
  font-size: 13px;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s ease;
  white-space: nowrap;
}

.copyButton:hover,
.saveButton:hover {
  background-color: var(--color-primary-dark);
}

.copyButton:active,
.saveButton:active {
  transform: scale(0.98);
}

.editorContent {
  flex: 1;
  overflow: auto;
  background-color: var(--color-background);
  min-height: 0;
}

.loading,
.empty {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 16px;
  padding: 40px 20px;
  text-align: center;
  color: var(--color-text-secondary);
}

.spinner {
  width: 32px;
  height: 32px;
  border: 3px solid var(--color-border);
  border-top-color: var(--color-primary);
  border-radius: 50%;
  animation: spin 0.6s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.codeBlocks {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.codeBlock {
  border-bottom: 2px solid var(--color-border-light);
}

.codeBlock:last-child {
  border-bottom: none;
}

.fileHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 16px;
  background-color: var(--color-surface);
  border-bottom: 1px solid var(--color-border);
  font-size: 12px;
  flex-shrink: 0;
}

.filePath {
  color: var(--color-text-secondary);
  font-family: monospace;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  flex: 1;
}

.fileIndex {
  color: var(--color-text-tertiary);
  background-color: var(--color-surface-secondary);
  padding: 2px 8px;
  border-radius: var(--radius-sm);
  flex-shrink: 0;
  margin-left: 12px;
}

.editorContent pre {
  margin: 0;
  padding: 0;
  background-color: var(--color-surface);
  overflow-x: auto;
  overflow-y: hidden;
  border-radius: 0;
}

.editorContent code {
  padding: 16px;
  display: block;
  font-family: monospace;
  font-size: 13px;
  line-height: 1.5;
  color: var(--color-text);
  white-space: pre;
  word-break: keep-all;
  overflow-wrap: normal;
}
